<template>
<div :style='{"width":"100%","padding":"30px 7% 40px","margin":"0px auto","position":"relative","background":"none"}'>
    <el-form
	  :style='{"border":"0px solid #28890b30","width":"100%","padding":"30px","position":"relative","background":"none"}'
      class="add-update-preview"
      ref="ruleForm"
      :model="ruleForm"
      :rules="rules"
      label-width="180px"
    >
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="养老院名称" prop="yanglaoyuanmingcheng">
            <el-input v-model="ruleForm.yanglaoyuanmingcheng" 
                placeholder="养老院名称" clearable :disabled=" false  ||ro.yanglaoyuanmingcheng"></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}'  label="规模" prop="guimo">
            <el-select v-model="ruleForm.guimo" placeholder="请选择规模" :disabled=" false  ||ro.guimo" >
              <el-option
                  v-for="(item,index) in guimoOptions"
                  :key="index"
                  :label="item"
                  :value="item">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="图片" v-if="type!='cross' || (type=='cross' && !ro.tupian)" prop="tupian">
            <file-upload
            tip="点击上传图片"
            action="file/upload"
            :limit="3"
            :multiple="true"
            :fileUrls="ruleForm.tupian?ruleForm.tupian:''"
            @change="tupianUploadChange"
            ></file-upload>
          </el-form-item>
            <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' class="upload" v-else label="图片" prop="tupian">
                <img v-if="ruleForm.tupian.substring(0,4)=='http'" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.tupian.split(',')[0]" width="100" height="100">
                <img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in ruleForm.tupian.split(',')" :src="baseUrl+item" width="100" height="100">
            </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="押金" prop="yajin">
            <el-input v-model.number="ruleForm.yajin" 
                placeholder="押金" clearable :disabled=" false  ||ro.yajin"></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="床位数量" prop="chuangweishuliang">
            <el-input v-model.number="ruleForm.chuangweishuliang" 
                placeholder="床位数量" clearable :disabled=" false  ||ro.chuangweishuliang"></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="收费标准" prop="shoufeibiaozhun">
            <el-input v-model="ruleForm.shoufeibiaozhun" 
                placeholder="收费标准" clearable :disabled=" false  ||ro.shoufeibiaozhun"></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="详细地址" prop="xiangxidizhi">
            <el-input v-model="ruleForm.xiangxidizhi" 
                placeholder="详细地址" clearable :disabled=" false  ||ro.xiangxidizhi"></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="咨询电话" prop="zixundianhua">
            <el-input v-model="ruleForm.zixundianhua" 
                placeholder="咨询电话" clearable :disabled=" false  ||ro.zixundianhua"></el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="住房介绍" prop="zhufangjieshao">
            <el-input
              type="textarea"
              :rows="8"
              placeholder="住房介绍"
              v-model="ruleForm.zhufangjieshao">
            </el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="设施介绍" prop="sheshijieshao">
            <el-input
              type="textarea"
              :rows="8"
              placeholder="设施介绍"
              v-model="ruleForm.sheshijieshao">
            </el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="入院流程" prop="ruyuanliucheng">
            <el-input
              type="textarea"
              :rows="8"
              placeholder="入院流程"
              v-model="ruleForm.ruyuanliucheng">
            </el-input>
          </el-form-item>
          <el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="养老院简介" prop="yanglaoyuanjianjie">
            <editor 
                :style='{"minHeight":"250px","padding":"0","margin":"0","borderColor":"#1abc9e30","backgroundColor":"#fff","borderRadius":"0","borderWidth":"0px","width":"100%","borderStyle":"solid","height":"auto"}'
                v-model="ruleForm.yanglaoyuanjianjie" 
                class="editor" 
                action="file/upload">
            </editor>
          </el-form-item>

      <el-form-item :style='{"padding":"0","margin":"0"}'>
        <el-button :style='{"border":"0","cursor":"pointer","padding":"0","margin":"0 20px 0 0","outline":"none","color":"rgba(255, 255, 255, 1)","borderRadius":"6px","background":"#1e3c4f","width":"128px","lineHeight":"36px","fontSize":"14px","height":"36px"}'  type="primary" @click="onSubmit">提交</el-button>
        <el-button :style='{"border":"1px solid #1e3c4f","cursor":"pointer","padding":"0","margin":"0","outline":"none","color":"#1e3c4f","borderRadius":"6px","background":"none","width":"128px","lineHeight":"40px","fontSize":"14px","height":"40px"}' @click="back()">返回</el-button>
      </el-form-item>
    </el-form>
</div>
</template>

<script>
  export default {
    data() {
	  let self = this
      return {
        id: '',
        baseUrl: '',
        ro:{
				yanglaoyuanmingcheng : false,
				guimo : false,
				tupian : false,
				zhufangjieshao : false,
				sheshijieshao : false,
				yajin : false,
				chuangweishuliang : false,
				ruyuanliucheng : false,
				shoufeibiaozhun : false,
				xiangxidizhi : false,
				zixundianhua : false,
				yanglaoyuanjianjie : false,
				clicktime : false,
				clicknum : false,
				discussnum : false,
				storeupnum : false,
        },
        type: '',
        userTableName: localStorage.getItem('UserTableName'),
        ruleForm: {
          yanglaoyuanmingcheng: '',
          guimo: '',
          tupian: '',
          zhufangjieshao: '',
          sheshijieshao: '',
          yajin: '',
          chuangweishuliang: '',
          ruyuanliucheng: '',
          shoufeibiaozhun: '',
          xiangxidizhi: '',
          zixundianhua: '',
          yanglaoyuanjianjie: '',
          clicktime: '',
          clicknum: '',
          discussnum: '',
          storeupnum: '',
        },
        guimoOptions: [],


        rules: {
          yanglaoyuanmingcheng: [
          ],
          guimo: [
          ],
          tupian: [
          ],
          zhufangjieshao: [
          ],
          sheshijieshao: [
          ],
          yajin: [
            { validator: this.$validate.isIntNumer, trigger: 'blur' },
          ],
          chuangweishuliang: [
            { validator: this.$validate.isIntNumer, trigger: 'blur' },
          ],
          ruyuanliucheng: [
          ],
          shoufeibiaozhun: [
          ],
          xiangxidizhi: [
          ],
          zixundianhua: [
            { validator: this.$validate.isMobile, trigger: 'blur' },
          ],
          yanglaoyuanjianjie: [
          ],
          clicktime: [
          ],
          clicknum: [
            { validator: this.$validate.isIntNumer, trigger: 'blur' },
          ],
          discussnum: [
            { validator: this.$validate.isIntNumer, trigger: 'blur' },
          ],
          storeupnum: [
            { validator: this.$validate.isIntNumer, trigger: 'blur' },
          ],
        },
		centerType: false,
      };
    },
    computed: {



    },
    components: {
    },
    created() {
		if(this.$route.query.centerType){
			this.centerType = true
		}
	  //this.bg();
      let type = this.$route.query.type ? this.$route.query.type : '';
      this.init(type);
      this.baseUrl = this.$config.baseUrl;
    },
    methods: {
      getMakeZero(s) {
          return s < 10 ? '0' + s : s;
      },
      // 下载
      download(file){
        window.open(`${file}`)
      },
      // 初始化
      init(type) {
        this.type = type;
        if(type=='cross'){
          var obj = JSON.parse(localStorage.getItem('crossObj'));
          for (var o in obj){
            if(o=='yanglaoyuanmingcheng'){
              this.ruleForm.yanglaoyuanmingcheng = obj[o];
              this.ro.yanglaoyuanmingcheng = true;
              continue;
            }
            if(o=='guimo'){
              this.ruleForm.guimo = obj[o];
              this.ro.guimo = true;
              continue;
            }
            if(o=='tupian'){
              this.ruleForm.tupian = obj[o].split(",")[0];
              this.ro.tupian = true;
              continue;
            }
            if(o=='zhufangjieshao'){
              this.ruleForm.zhufangjieshao = obj[o];
              this.ro.zhufangjieshao = true;
              continue;
            }
            if(o=='sheshijieshao'){
              this.ruleForm.sheshijieshao = obj[o];
              this.ro.sheshijieshao = true;
              continue;
            }
            if(o=='yajin'){
              this.ruleForm.yajin = obj[o];
              this.ro.yajin = true;
              continue;
            }
            if(o=='chuangweishuliang'){
              this.ruleForm.chuangweishuliang = obj[o];
              this.ro.chuangweishuliang = true;
              continue;
            }
            if(o=='ruyuanliucheng'){
              this.ruleForm.ruyuanliucheng = obj[o];
              this.ro.ruyuanliucheng = true;
              continue;
            }
            if(o=='shoufeibiaozhun'){
              this.ruleForm.shoufeibiaozhun = obj[o];
              this.ro.shoufeibiaozhun = true;
              continue;
            }
            if(o=='xiangxidizhi'){
              this.ruleForm.xiangxidizhi = obj[o];
              this.ro.xiangxidizhi = true;
              continue;
            }
            if(o=='zixundianhua'){
              this.ruleForm.zixundianhua = obj[o];
              this.ro.zixundianhua = true;
              continue;
            }
            if(o=='yanglaoyuanjianjie'){
              this.ruleForm.yanglaoyuanjianjie = obj[o];
              this.ro.yanglaoyuanjianjie = true;
              continue;
            }
            if(o=='clicktime'){
              this.ruleForm.clicktime = obj[o];
              this.ro.clicktime = true;
              continue;
            }
            if(o=='clicknum'){
              this.ruleForm.clicknum = obj[o];
              this.ro.clicknum = true;
              continue;
            }
            if(o=='discussnum'){
              this.ruleForm.discussnum = obj[o];
              this.ro.discussnum = true;
              continue;
            }
            if(o=='storeupnum'){
              this.ruleForm.storeupnum = obj[o];
              this.ro.storeupnum = true;
              continue;
            }
          }
        }else if(type=='edit'){
			this.info()
		}
        // 获取用户信息
        this.$http.get(this.userTableName + '/session', {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            var json = res.data.data;
          }
        });
        this.guimoOptions = "大型,小型".split(',')

		if (localStorage.getItem('raffleType') && localStorage.getItem('raffleType') != null) {
			localStorage.removeItem('raffleType')
			setTimeout(() => {
				this.onSubmit()
			}, 300)
		}
      },

    // 多级联动参数
      // 多级联动参数
      info() {
        this.$http.get(`yanglaoyuan/detail/${this.$route.query.id}`, {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            this.ruleForm = res.data.data;
          }
        });
      },
      // 提交
      onSubmit() {

			//更新跨表属性
			var crossuserid;
			var crossrefid;
			var crossoptnum;
			this.$refs["ruleForm"].validate(valid => {
				if(valid) {
					if(this.type=='cross'){
						var statusColumnName = localStorage.getItem('statusColumnName');
						var statusColumnValue = localStorage.getItem('statusColumnValue');
						if(statusColumnName && statusColumnName!='') {
							var obj = JSON.parse(localStorage.getItem('crossObj'));
							if(!statusColumnName.startsWith("[")) {
								for (var o in obj){
									if(o==statusColumnName){
										obj[o] = statusColumnValue;
									}
								}
								var table = localStorage.getItem('crossTable');
								this.$http.post(table+'/update', obj).then(res => {});
							} else {
								crossuserid=Number(localStorage.getItem('frontUserid'));
								crossrefid=obj['id'];
								crossoptnum=localStorage.getItem('statusColumnName');
								crossoptnum=crossoptnum.replace(/\[/,"").replace(/\]/,"");
							}
						}
					}
					if(crossrefid && crossuserid) {
						this.ruleForm.crossuserid=crossuserid;
						this.ruleForm.crossrefid=crossrefid;
						var params = {
							page: 1,
							limit: 10,
							crossuserid:crossuserid,
							crossrefid:crossrefid,
						}
						this.$http.get('yanglaoyuan/list', {
							params: params
						}).then(res => {
							if(res.data.data.total>=crossoptnum) {
								this.$message({
									message: localStorage.getItem('tips'),
									type: 'error',
									duration: 1500,
								});
								return false;
							} else {
								// 跨表计算


								this.$http.post(`yanglaoyuan/${this.ruleForm.id?'update':this.centerType?'save':'add'}`, this.ruleForm).then(res => {
									if (res.data.code == 0) {
										this.$message({
											message: '操作成功',
											type: 'success',
											duration: 1500,
											onClose: () => {
												this.$router.go(-1);
											}
										});
									} else {
										this.$message({
											message: res.data.msg,
											type: 'error',
											duration: 1500
										});
									}
								});
							}
						});
					} else {


						this.$http.post(`yanglaoyuan/${this.ruleForm.id?'update':this.centerType?'save':'add'}`, this.ruleForm).then(res => {
							if (res.data.code == 0) {
								this.$message({
									message: '操作成功',
									type: 'success',
									duration: 1500,
									onClose: () => {
										this.$router.go(-1);
									}
								});
							} else {
								this.$message({
									message: res.data.msg,
									type: 'error',
									duration: 1500
								});
							}
						});
					}
				}
			});
		},
		// 获取uuid
		getUUID () {
			return new Date().getTime();
		},
		// 返回
		back() {
			this.$router.go(-1);
		},
      tupianUploadChange(fileUrls) {
          this.ruleForm.tupian = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
      },
    }
  };
</script>

